<template>
<div>
  <el-tabs type="border-card">
    <el-tab-pane label="">
      <el-form label-position="left" :model="provinceForm" :rules="rules" ref="provinceForm" :inline="true">
        <el-form-item label="" prop="province">
                    <el-select v-model="provinceForm.province" placeholder="">
                      <el-option
                        v-for="item in provinceOptions"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                      </el-option>
                    </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="Submit3"></el-button>
        </el-form-item>
      </el-form>
      <el-row v-if="p_flag">
        <el-col :span="12">
          <el-table
            :data="provinceData"
            border
            style="width: 100%">
            <el-table-column
              prop="city"
              label=""
              width="100">
            </el-table-column>
            <el-table-column
              label=""
              prop="high_level_number"
              width="160">
              <template slot-scope="scope">
                <el-tag size="small" type="danger">
                  {{scope.row.high_level_number}}
                </el-tag>
              </template>
            </el-table-column>
            <el-table-column
              prop="medium_level_number"
              label=""
              width="160">
              <template slot-scope="scope">
                <el-tag size="small" type="warning">
                  {{scope.row.medium_level_number}}
                </el-tag>
              </template>
            </el-table-column>
            <el-table-column
              prop="low_level_number"
              label="">
              <template slot-scope="scope">
                <el-tag size="small" type="success">
                  {{scope.row.low_level_number}}
                </el-tag>
              </template>
            </el-table-column>
          </el-table>
        </el-col>
        <el-col :span="12">
          <e-charts class="chart" :option="POption"/>
        </el-col>
      </el-row>

    </el-tab-pane>
    <el-tab-pane label="">
      <el-form label-position="left" :model="form1" :rules="rules" ref="form1" :inline="true">
        <el-form-item label="" prop="selectedOptions">
          <el-cascader
            placeholder=""
            size="large"
            :options="options1"
            v-model="form1.selectedOptions">
          </el-cascader>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="Submit1"></el-button>
        </el-form-item>
      </el-form>
      <el-row v-if="flag1">
        <el-col :span="10">
          <el-descriptions  direction="vertical" :column="3" border>
            <el-descriptions-item label="" :span="3">
              {{(getCodeToText(this.form1.selectedOptions)||"").replace(/:/g,'')}}
            </el-descriptions-item>
            <el-descriptions-item label="">
              <el-tag size="small" type="danger">{{ overallData.high_level_number }}</el-tag>
            </el-descriptions-item>
            <el-descriptions-item label="" >
              <el-tag size="small" type="warning">{{ overallData.medium_level_number }}</el-tag>
            </el-descriptions-item>
            <el-descriptions-item label="">
              <el-tag size="small" type="success">{{ overallData.low_level_number }}</el-tag>
            </el-descriptions-item>

          </el-descriptions>
        </el-col>
        <el-col :span="14">
          <e-charts class="chart" :option="COption"/>
        </el-col>
      </el-row>
    </el-tab-pane>

    <el-tab-pane label="">
      <el-form label-position="left" :model="form" :rules="rules" ref="form" :inline="true">
      <el-form-item label="" prop="selectedOptions">
        <el-cascader
          placeholder=""
          size="large"
          :options="options"
          v-model="form.selectedOptions">
        </el-cascader>
      </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="Submit2"></el-button>
        </el-form-item>
      </el-form>
      <el-row v-if="flag">
        <el-col :span="10">
          <el-descriptions  direction="vertical" :column="2" border>
            <el-descriptions-item label="" :span="2">
              {{(getCodeToText(this.form.selectedOptions)||"").replace(/:/g,'')}}
            </el-descriptions-item>
            <el-descriptions-item label="">
              <el-tag size="small" :type="riskTag(specificData.risk_level)">{{ riskLevel(specificData.risk_level) }}</el-tag>
            </el-descriptions-item>
            <el-descriptions-item label="" >
              <el-tag size="small" type="danger">{{ specificData.positive_number }}</el-tag>
            </el-descriptions-item>


          </el-descriptions>
        </el-col>
      </el-row>
    </el-tab-pane>
  </el-tabs>
</div>
</template>

<script>
import { regionData,CodeToText,provinceAndCityData,TextToCode } from 'element-china-area-data'
import { getAllSituation } from "@/api/getAllSituation";
import cookie from "js-cookie";
import { getAreaSituation } from "@/api/getAreaSituation";
import { getProvinceSituation } from "@/api/getProvinceSituation";
export default {
  name: "RiskRegion",
  data(){
    return{
      sum1:0,//high
      sum2:0,//medium
      sum3:0,//low
      CName:'',
      DName:'',
      PName:'',
      provinceData:[],
      specificData:{
        risk_level:"",
        positive_number:"",
      },
      overallData:{
        high_level_number:"",
        medium_level_number:"",
        low_level_number:"",
      },
      flag1:false,
      p_flag:false,
      flag:false,
      value1:123,
      provinceOptions:[
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
      ],
      options: regionData,
      options1: provinceAndCityData,
      provinceForm:{
        selectedOptions:'',
        province:'',
      },
      form1:{
        selectedOptions:'',
        province:'',
        city:'',
      },
      form:{
        selectedOptions:'',
      },
      rules:{
        selectedOptions:[
          {
            required: true,
            message: '',
            trigger: 'change',
          }
        ],
        province:[
          {
            required: true,
            message: '',
            trigger: 'change',
          }
        ],
    }
  }
  },
  computed:{
    POption(){
      return{
        title: {
          text: '',
          subtext:this.PName,
          left: 'center'
        },
        tooltip: {
          trigger: 'item'
        },
        legend: {
          orient: 'horizontal',
          left: 'center',
          top:'bottom',
        },
        series: [
          {
            name: '',
            type: 'pie',
            radius: '50%',
            data: [
              { value: this.sum3, name: '',itemStyle: { color: '#66c03b' } },
              { value: this.sum2, name: '',itemStyle: { color: '#e8a657' } },
              { value: this.sum1, name: '',itemStyle: { color: '#f17173' } }
            ],
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: 'rgba(0, 0, 0, 0.5)'
              }
            }
          }
        ]
      }
    },
    COption(){
      return{
        title: {
          text: '',
          subtext:this.CName,
          left: 'center'
        },
        tooltip: {
          trigger: 'item'
        },
        legend: {
          orient: 'horizontal',
          left: 'center',
          top:'bottom',
        },
        series: [
          {
            name: '',
            type: 'pie',
            radius: '50%',
            data: [
              { value: this.overallData.low_level_number, name: '',itemStyle: { color: '#66c03b' } },
              { value: this.overallData.medium_level_number, name: '',itemStyle: { color: '#e8a657' } },
              { value: this.overallData.high_level_number, name: '',itemStyle: { color: '#f17173' } }
            ],
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: 'rgba(0, 0, 0, 0.5)'
              }
            }
          }
        ]
      }
    },
  },
  methods:{
    riskTag(level){
      if(level<=0){
        return "info"
      }else if(level===1){
        return "success"
      }else if(level===2){
        return "warning"
      }else if(level==3){
        return "danger"
      }
    },
    riskLevel(level){
      if(level<=0){
        return ""
      }else if(level===1){
        return ""
      }else if(level===2){
        return ""
      }else if(level==3){
        return ""
      }
    },
    getCodeToText(codeArray) {
      // if (null === codeStr && null === codeArray) {
      //   return null;
      // } else if (null === codeArray) {
      //   codeArray = codeStr.split(",");
      // }
      let area = "";
      switch (codeArray.length) {
        case 1:
          area += CodeToText[codeArray[0]];
          break;
        case 2:
          area += CodeToText[codeArray[0]] + ":" + CodeToText[codeArray[1]];
          break;
        case 3:
          area +=
            CodeToText[codeArray[0]] +
            ":" +
            CodeToText[codeArray[1]] +
            ":" +
            CodeToText[codeArray[2]];
          break;
        default:
          break;
      }
      return area;
    },
    Submit1(){//city
      this.$refs.form1.validate(valid=>{
        if(valid){
          let tmp=this.getCodeToText(this.form1.selectedOptions)
          this.form1.province=tmp.split(':')[0]
          this.form1.city=tmp.split(':')[1]
          this.CName=this.form1.province+this.form1.city
          console.log(this.form1.province,this.form1.city)
          let param={token:cookie.get('token'), province:this.form1.province, city:this.form1.city}
          getAllSituation(param).then(res=>{
            this.overallData=res.data.data
            this.flag1=true
            console.log(res)
          })
        }
      })
    },
    Submit3(){//
      this.$refs.provinceForm.validate(valid=>{
        if(valid){
          this.PName=this.provinceForm.province
          let param={token:cookie.get('token'), province:this.provinceForm.province}
          getProvinceSituation(param).then(res=>{
            this.provinceData=res.data.data.cities
            let len=this.provinceData.length//
            this.sum1=0;this.sum2=0;this.sum3=0;
            for(let i=0;i<len;i++){
              this.sum1+=this.provinceData[i].high_level_number
              this.sum2+=this.provinceData[i].medium_level_number
              this.sum3+=this.provinceData[i].low_level_number
            }
            this.p_flag=true
            console.log(res)
          }).catch(err=>{
            console.log(err)
          })
        }
      })
    },
    Submit2(){//district
      this.$refs.form.validate(valid=>{
        if(valid){
          let tmp=this.getCodeToText(this.form.selectedOptions)
          this.form.province=tmp.split(':')[0]
          this.form.city=tmp.split(':')[1]
          this.form.district=tmp.split(':')[2]
          this.DName=this.form.province+this.form.city+this.form.district
          let param={token:cookie.get('token'), province:this.form.province, city:this.form.city,district:this.form.district}
          getAreaSituation(param).then(res=>{
            this.specificData=res.data.data
            this.flag=true
            console.log(res)
          })
        }
      })
    },
  }
};
</script>

<style lang="less" scoped>
.chart{
  height: 400px;
}
.my-label {
  background: #E1F3D8;
}

.my-content {
  background: #FDE2E2;
}
</style>