<template>
  <el-tabs type="border-card">
    <el-tab-pane label="">
      <el-form label-position="left" :model="form" :rules="rules" ref="form" :inline="true">
        <el-form-item label="" prop="selectedOptions">
          <el-select v-model="form.selectedOptions" placeholder="">
            <el-option
              v-for="item in provinceOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="Submit"></el-button>
        </el-form-item>
      </el-form>
      <el-row v-if="p_flag">
        <el-col :span="10">
          <el-descriptions  direction="vertical" :column="3" border>
            <el-descriptions-item label="" :span="3">
              {{this.form.selectedOptions}}
            </el-descriptions-item>
            <el-descriptions-item label="" >
              <el-tag size="small">{{ provinceData.negative+provinceData.positive }}</el-tag>
            </el-descriptions-item>
            <el-descriptions-item label="" >
              <el-tag size="small" type="success">{{ provinceData.negative }}</el-tag>
            </el-descriptions-item>
            <el-descriptions-item label="">
              <el-tag size="small" type="danger">{{ provinceData.positive}}</el-tag>
            </el-descriptions-item>
          </el-descriptions>
        </el-col>
        <el-col :span="14">
          <e-charts class="chart" :option="POption"/>
        </el-col>
      </el-row>
    </el-tab-pane>

    <el-tab-pane label="">
      <el-form label-position="left" :model="cityForm" :rules="rules" ref="cityForm" :inline="true">
        <el-form-item label="" prop="selectedOptions">
          <el-cascader
            placeholder=""
            size="large"
            :options="cityOptions"
            v-model="cityForm.selectedOptions">
          </el-cascader>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="citySubmit"></el-button>
        </el-form-item>
      </el-form>
      <el-row v-if="c_flag">
        <el-col :span="10">
          <el-descriptions  direction="vertical" :column="3" border>
            <el-descriptions-item label="" :span="3">
              {{(getCodeToText(this.cityForm.selectedOptions)||"").replace(/:/g,'')}}
            </el-descriptions-item>
            <el-descriptions-item label="" >
              <el-tag size="small">{{ cityData.negative+cityData.positive }}</el-tag>
            </el-descriptions-item>
            <el-descriptions-item label="" >
              <el-tag size="small" type="success">{{ cityData.negative }}</el-tag>
            </el-descriptions-item>
            <el-descriptions-item label="">
              <el-tag size="small" type="danger">{{ cityData.positive}}</el-tag>
            </el-descriptions-item>
          </el-descriptions>
        </el-col>
        <el-col :span="14">
          <e-charts class="chart" :option="COption"/>
        </el-col>
      </el-row>
    </el-tab-pane>

    <el-tab-pane label="">
      <el-form label-position="left" :model="districtForm" :rules="rules" ref="districtForm" :inline="true">
        <el-form-item label="" prop="selectedOptions">
          <el-cascader
            placeholder=""
            size="large"
            :options="districtOptions"
            v-model="districtForm.selectedOptions">
          </el-cascader>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="districtSubmit"></el-button>
        </el-form-item>
      </el-form>
      <el-row v-if="d_flag">
        <el-col :span="10">
          <el-descriptions  direction="vertical" :column="3" border>
            <el-descriptions-item label="" :span="3">
              {{(getCodeToText(this.districtForm.selectedOptions)||"").replace(/:/g,'')}}
            </el-descriptions-item>
            <el-descriptions-item label="" >
              <el-tag size="small">{{ districtData.negative+districtData.positive }}</el-tag>
            </el-descriptions-item>
            <el-descriptions-item label="" >
              <el-tag size="small" type="success">{{ districtData.negative }}</el-tag>
            </el-descriptions-item>
            <el-descriptions-item label="">
              <el-tag size="small" type="danger">{{ districtData.positive}}</el-tag>
            </el-descriptions-item>
          </el-descriptions>
        </el-col>
        <el-col :span="14">
          <e-charts class="chart" :option="DOption"/>
        </el-col>
      </el-row>
    </el-tab-pane>
  </el-tabs>
</template>

<script>
import cookie from "js-cookie";
import { getTestInfo } from "@/api/getTestInfo";
import {regionData,CodeToText,provinceAndCityData,TextToCode } from 'element-china-area-data'
export default {
  name: "TestResult",
  data(){
    return{
      CName:'',
      DName:'',
      PName:'',
      p_flag:false,
      c_flag:false,
      d_flag:false,
      provinceOptions:[
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
      ],
      form:{
        selectedOptions:'',
        province:'',
        city:'',
        district:'',
      },
      cityForm:{
        selectedOptions:'',
        province:'',
        city:'',
        district:'',
      }, districtForm:{
        selectedOptions:'',
        province:'',
        city:'',
        district:'',
      },
      districtOptions: regionData,
      cityOptions: provinceAndCityData,
      provinceData:{
        positive:'',
        negative:'',
      },
      cityData:{
        positive:'',
        negative:'',
      },
      districtData:{
        positive:'',
        negative:'',
      },
      rules:{
        selectedOptions:[
          {
            required: true,
            message: '',
            trigger: 'change'
          }
        ],
      },
    }
  },
  computed:{
    POption(){
      return{
        title: {
          text: '',
          subtext:this.PName,
          left: 'center'
        },
        tooltip: {
          trigger: 'item'
        },
        legend: {
          orient: 'horizontal',
          left: 'center',
          top:'bottom',
        },
        series: [
          {
            name: '',
            type: 'pie',
            radius: '50%',
            data: [
              { value: this.provinceData.negative, name: '',itemStyle: { color: '#66c03b' } },
              { value: this.provinceData.positive, name: '',itemStyle: { color: '#f17173' } }
            ],
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: 'rgba(0, 0, 0, 0.5)'
              }
            }
          }
        ]
      }
    },
    COption(){
      return{
        title: {
          text: '',
          subtext:this.CName,
          left: 'center'
        },
        tooltip: {
          trigger: 'item'
        },
        legend: {
          orient: 'horizontal',
          left: 'center',
          top:'bottom',
        },
        series: [
          {
            name: '',
            type: 'pie',
            radius: '50%',
            data: [
              { value: this.cityData.negative, name: '',itemStyle: { color: '#66c03b' } },
              { value: this.cityData.positive, name: '',itemStyle: { color: '#f17173' } }
            ],
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: 'rgba(0, 0, 0, 0.5)'
              }
            }
          }
        ]
      }
    },
    DOption(){
      return{
        title: {
          text: '',
          subtext:this.DName,
          left: 'center'
        },
        tooltip: {
          trigger: 'item'
        },
        legend: {
          orient: 'horizontal',
          left: 'center',
          top:'bottom',
        },
        series: [
          {
            name: '',
            type: 'pie',
            radius: '50%',
            data: [
              { value: this.districtData.negative, name: '',itemStyle: { color: '#66c03b' } },
              { value: this.districtData.positive, name: '',itemStyle: { color: '#f17173' } }
            ],
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: 'rgba(0, 0, 0, 0.5)'
              }
            }
          }
        ]
      }
    },
  },
  methods:{
    getCodeToText(codeArray) {
      // if (null === codeStr && null === codeArray) {
      //   return null;
      // } else if (null === codeArray) {
      //   codeArray = codeStr.split(",");
      // }
      let area = "";
      switch (codeArray.length) {
        case 1:
          area += CodeToText[codeArray[0]];
          break;
        case 2:
          area += CodeToText[codeArray[0]] + ":" + CodeToText[codeArray[1]];
          break;
        case 3:
          area +=
            CodeToText[codeArray[0]] +
            ":" +
            CodeToText[codeArray[1]] +
            ":" +
            CodeToText[codeArray[2]];
          break;
        default:
          break;
      }
      return area;
    },
    Submit(){
      this.$refs.form.validate(valid=>{
        if(valid){
          this.PName=this.form.selectedOptions
          let param={token:cookie.get('token'), province:this.form.selectedOptions}
          getTestInfo(param).then(res=>{
            this.provinceData=res.data.data
            this.p_flag=true
          }).catch(err=>{
            console.log(err)
          })
        }
      })
    },
    citySubmit(){
      this.$refs.cityForm.validate(valid=>{
        if(valid){
          let tmp=this.getCodeToText(this.cityForm.selectedOptions)
          this.cityForm.province=tmp.split(':')[0]
          this.cityForm.city=tmp.split(':')[1]
          console.log( this.cityForm.province,this.cityForm.city)
          this.CName=this.cityForm.province+this.cityForm.city
          let param={token:cookie.get('token'), province:this.cityForm.province, city:this.cityForm.city}
          getTestInfo(param).then(res=>{
            console.log(res)
            this.cityData=res.data.data
            this.c_flag=true
          }).catch(err=>{
            console.log(err)
          })
        }
      })
    },
    districtSubmit(){
      this.$refs.districtForm.validate(valid=>{
        if(valid){
          let tmp=this.getCodeToText(this.districtForm.selectedOptions)
          this.districtForm.province=tmp.split(':')[0]
          this.districtForm.city=tmp.split(':')[1]
          this.districtForm.district=tmp.split(':')[2]
          this.DName=this.districtForm.province+this.districtForm.city+this.districtForm.district
          let param={token:cookie.get('token'), province:this.districtForm.province, city:this.districtForm.city, district:this.districtForm.district}
          getTestInfo(param).then(res=>{
            console.log(res)
            this.districtData=res.data.data
            this.d_flag=true
          }).catch(err=>{
            console.log(err)
          })
        }
      })
    },
  },

};
</script>

<style scoped>
.chart{
  height: 400px;
}
</style>