<template>
<div class="complaintBox">

  <el-card class="box-card" style="border-radius: 10px;height: 85vh;position: relative">
    <!--    -->
    <!--    -->
  <el-table
    :data="infoList"
    stripe
    height="100vh"
    style="width: 100%">
    <el-table-column
      prop="application_id"
      label=""
      width="180">
    </el-table-column>
    <el-table-column
      prop="person_name"
      label=""
      width="180">
    </el-table-column>
    <el-table-column
      prop="person_id"
      label=""
      width="180">
    </el-table-column>
    <el-table-column
      prop="phone_number"
      label=""
      width="180">
    </el-table-column>
    <el-table-column
      label="">
      <template slot-scope="scope">
        {{showType(scope.row.type)}}
      </template>
    </el-table-column>
    <el-table-column
      align="right"
      label="">
      <template slot-scope="scope">
        <el-button
          @click.native.prevent="handleRow(scope.row)"
          type="text"
          size="small">
          
        </el-button>
      </template>
    </el-table-column>
    <el-table-column
      fixed="right"
      width="50">
    <template slot="header" slot-scope="scope">
      <el-button icon="el-icon-refresh" circle @click="handleRefresh"></el-button>
    </template>
    </el-table-column>

  </el-table>
    <!--    -->
    <div class="pageBox" style="bottom:0;position: absolute;width: 100%">
      <el-pagination
        layout="prev, pager, next"
        :current-page.sync="currentPage"
        :page-size="pageSize"
        @current-change="handleChange"
        :total="total">
      </el-pagination>
    </div>
  </el-card>

  <!---->
  <el-dialog
    title=""
    :visible.sync="dialogVisible"
    width="50%">

    <el-descriptions title="">
      <el-descriptions-item label="">{{ form.application_id }}</el-descriptions-item>
      <el-descriptions-item label="">{{form.person_name}}</el-descriptions-item>
      <el-descriptions-item label="">{{ form.phone_number }}</el-descriptions-item>
      <el-descriptions-item label="">{{ form.person_id }}</el-descriptions-item>
      <el-descriptions-item label="">{{showType(form.type)}}</el-descriptions-item>
      <el-descriptions-item label="">
        {{ (form.additional_info===null||form.additional_info==='')?'':form.additional_info }}
      </el-descriptions-item>
    </el-descriptions>
    <el-form :model="form" :rules="rules" ref="form" :hide-required-asterisk="true">
      <el-form-item label="" prop="is_processed">
        <el-select v-model="form.is_processed" placeholder="">
          <el-option label="" value="1"></el-option>
          <el-option label="" value="0"></el-option>
        </el-select>
      </el-form-item>
    </el-form>

    <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false"> </el-button>
    <el-button type="primary" @click="handleSubmit"> </el-button>
  </span>
  </el-dialog>
</div>
</template>

<script>
import cookie from "js-cookie";
import { getComplaint } from "@/api/getComplaint";
import { postComplaint } from "@/api/postComplaint";
export default {
  name: "ComplaintList",
  data(){
    return{
      pageSize:8,//
      currentPage:1,// 
      total:0,//,
      rules:{
        is_processed:[
          {
            required: true,
            message: '',
            trigger: 'change',
          }
        ]
      },
      form:{
        additional_info: "",
        person_name: "fy",
        phone_number: "13317278716",
        application_id:"15896953",
        is_processed:0,
        is_investigated:0,
        type:""
    },
      infoList:[],
      dialogVisible:false,
      testList:[
        {
          additional_info: "",
          person_name: "fy",
          phone_number: "13317278716",
          application_id:"15896953",
          is_processed:0,
          is_investigated:0,
          type:""
        },
        {
          additional_info: "",
          person_name: "fy",
          phone_number: "13317278716",
          application_id:"15896953",
          is_processed:0,
          is_investigated:0,
          type:""
        },
      ]
    }
  },
  methods:{
    showType(type){
      // 0 
      // 1 
      // 2 
      // 3 
      if(type===0){
        return ""
      }else if(type===1){
        return ""
      }else if(type===2){
        return ""
      }else if(type===3){
        return ""
      }
    },
    handleRefresh(){
      this.currentPage=1
      this.getInfo(this.currentPage)
    },
    getInfo(curPage){
      let param={token:cookie.get('token'), page:curPage, size:this.pageSize}
      getComplaint(param).then(res=>{
        console.log(res)
        this.infoList=res.data.data.abnormal_list
        this.total=res.data.data.total
        this.$message({
          message: '',
          type: 'success'
        });
      }).catch(err=>{
        console.log(err)
        this.$message.error('!')
        const h = this.$createElement;
        this.$notify({
          title: '',
          message: h('i', { style: 'color: teal'}, '')
        });
      })
    },

    handleChange(val){
      console.log(val)
      this.getInfo(this.currentPage)
    },
    handleRow(row){
      if (this.$refs['form']!==undefined) {//
        this.$refs['form'].resetFields();
      }
      this.form=JSON.parse(JSON.stringify(row))
      this.form.is_processed=null
      this.dialogVisible=true
    },
    showState(){
      if(this.form.is_processed===1){
        return ""
      }else
        return ""
    },
    handleSubmit(){
      this.$refs.form.validate((valid)=>{
        if(valid){
          this.dialogVisible = false;
          this.form["token"]=cookie.get('token')
          console.log(this.form)
          postComplaint(this.form).then(res=>{
            console.log(res)
          })
          this.currentPage=1
          this.getInfo(this.currentPage)
        }
      })
    }
  },
  created() {
    //
    //    let info=await getTester(cookie.get('token'))
    //    this.infoList=info.data.application_list
    //    this.total=this.infoList.length
    this.getInfo(this.currentPage)
    console.log('info',this.infoList)
    console.log('total',this.total)
  }


};
</script>

<style lang="less" scoped>
.complaintBox{
  height: 98%;
  .pageBox{
    text-align: center;
  }
}
</style>